@import '../common/styles/sass/variables.module';


.projectsBlock {
  min-height: 80vh;
  background-color: #111111;
  padding-bottom: 65px;
  .particles{
    position: absolute;
    left: 0; right: 0; /* Вся ширина */
    top: 0;
    bottom: 0;
    opacity: 0.3;
  }
  .projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    .projects {

      margin-top: 50px;
      width: 100%;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      @media screen and (max-width: 1332px) {
        justify-content: space-evenly;
      }
    }
  }
  @media screen and (max-width: 1332px) {
    min-height: 70vh;
  }

}

